<script>
// $jquery
import $ from 'jquery';

import LoadingPage from '@/components/common/Loading_page.vue';
import Sidebar from '@/components/front/Sidebar.vue';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import 'swiper/css/swiper.css';

export default {
  name: 'Product_detail',
  components: {
    LoadingPage,
    Sidebar,
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      // swiper 
      swiperOption: {
        // 
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        // 
        slidesPerView: 4,
        // slidepx
        spaceBetween: 50,
        // 
        breakpoints: {
          1024: {
            slidesPerView: 3,
            spaceBetween: 40,
          },
          768: {
            slidesPerView: 2,
            spaceBetween: 30,
          },
          375: {
            slidesPerView: 1,
            spaceBetween: 20,
          },
        },
      },
      // 
      product: {},
      // 
      cart: [],
      // 
      tempQty: 1,
      // 
      recordProducts: [],
      // 
      status: {
        // 
        pageIsLoading: false,
      },
    };
  },
  methods: {
    // 
    getProductDetail() {
      const vm = this;
      // 
      vm.status.pageIsLoading = true;

      // id
      let id = '';
      id = vm.$route.params.id;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      vm.$http.get(api).then((response) => {
        // 
        if (response.data.success) {
          // 
          vm.product = response.data.product;
          // 
          vm.saveRecord();
        } else {
          vm.$router.push({ path: '/products/all' });
        }

        // 
        vm.status.pageIsLoading = false;
      });
    },
    // 
    addCartQty(qty) {
      const vm = this;
      // 
      let tempQty = qty;
      // 
      // 0
      if (tempQty <= 0) {
        // 1
        tempQty = 1;
      } else if (tempQty > 99) {
        // 99
        // 99
        tempQty = 99;
      }
      // 
      vm.tempQty = tempQty;
    },
    // 
    getCart() {
      const vm = this;
      //  localStorage
      const data = JSON.parse(localStorage.getItem('cart'));
      // 
      if (data) {
        vm.cart = data;

        // 
        let total = 0;
        vm.cart.forEach((item) => {
          total += item.qty;
        });

        // 
        vm.$bus.$emit('upateCartQty', total);
      }
    },
    // 
    addtoCart(data, event) {
      const vm = this;
      // 
      const i = '<i class="fas fa-spinner fa-spin"></i>';
      $(event.target).append(i);
      // 
      $(event.target).attr('disabled', true);

      // 
      const check = vm.cart.findIndex((item) => item.product_id === data.id);
      // 
      if (check === -1) {
        const product = {
          product_id: data.id,
          qty: vm.tempQty,
          title: data.title,
          price: data.price,
          imageUrl: data.imageUrl,
        };
        vm.cart.push(product);
      } else {
        // 
        vm.cart[check].qty += vm.tempQty;
      }

      // localStorage
      new Promise((resolve) => {
        resolve(localStorage.setItem('cart', JSON.stringify(vm.cart)));
      }).then(() => {
        // 
        vm.$bus.$emit('upateCartQty');

        setTimeout(() => {
          // 
          $(event.target)
            .children()
            .remove();
          // 
          $(event.target).attr('disabled', false);
        }, 1000);
      });
    },
    // 
    saveRecord() {
      const vm = this;
      // 
      const check = vm.recordProducts.find((item) => item.id === vm.product.id);
      // 
      if (check === undefined) {
        vm.recordProducts.push(vm.product);
      }

      //  localStorage
      localStorage.setItem('save', JSON.stringify(vm.recordProducts));
    },
    // 
    getRecord() {
      //  localStorage
      const data = JSON.parse(localStorage.getItem('save'));
      // 
      if (data) {
        this.recordProducts = data;
      }
    },
    // 
    goToProductDetail(productId) {
      const vm = this;
      let id = '';
      id = vm.$route.params.id;

      // 
      if (id === productId) {
        // 
        $('html,body').scrollTop(0);
      } else {
        // 
        vm.$router.push({ path: `/product_detail/${productId}` });
      }
    },
  },
  watch: {
    // 
    tempQty(value) {
      const vm = this;
      // 
      let tempQty = value;
      // 
      // 0
      if (tempQty <= 0) {
        // 1
        tempQty = 1;
      } else if (tempQty > 99) {
        // 99
        // 99
        tempQty = 99;
      }
      // 
      vm.tempQty = tempQty;
    },
    // 
    $route() {
      const vm = this;
      // 
      vm.getProductDetail();

      // 
      $('html,body').scrollTop(0);
    },
  },
  mounted() {
    // 
    $('html,body').scrollTop(0);
  },
  created() {
    const vm = this;
    vm.getProductDetail();
    vm.getCart();
    vm.getRecord();
  },
};
</script>

