                    {{ cart.final_total | currency }}
                  </td>
                </tr>
              </tbody>
              <!--  -->
              <tfoot v-if="cart.carts">
                <tr v-if="status.is_paid === false">
                  <td colspan="5" class="coupon pl-0 pr-0 pb-4">
                    <p class="pl-2 pb-1">
                      ?
                      <a
                        class="coupon__link"
                        data-toggle="collapse"
                        href="#collapseCoupon"
                        role="button"
                        aria-expanded="false"
                        aria-controls="collapseCoupon"
                      >
                        
                      </a>
                    </p>
                    <div class="input-group collapse pr-0" id="collapseCoupon">
                      <input
                        type="text"
                        class="form-control coupon__input"
                        placeholder=""
                        aria-describedby="couponBtn"
                        v-model.trim="coupon"
                      />
                      <div class="input-group-prepend">
                        <button
                          class="btn btn--baseSet btn--secColor coupon__input"
                          id="couponBtn"
                          @click="postCoupon"
                        >
                          
                        </button>
                      </div>
                    </div>
                    <p class="text-danger mt-2" v-if="couponFeedBack !== ''">
                      {{ couponFeedBack }}
                    </p>
                  </td>
                </tr>
              </tfoot>
              <!-- End  -->
            </table>
          </div>
          <!-- End  -->

          <!--  -->
          <div
            class="col-12 col-lg-6 mt-1 mt-lg-1"
            v-if="status.is_paid === false"
          >
            <h2 class="subtitle text-center pt-5 pt-3-md pb-3"></h2>
            <validation-observer v-slot="{ invalid }" ref="checkoutForm">
              <form class="checkout-form" @submit.prevent="submitCheckoutData">
                <div class="form-row checkout-form--pt">
                  <div class="form-group col-md">
                    <validation-provider
                      :rules="{
                        regex: /^[\u4E00-\u9FA5]{2,}$/,
                        required: 'required',
                      }"
                      v-slot="{ errors, classes }"
                    >
                      <label for=""
                        > <span class="text-danger">*</span></label
                      >
                      <input
                        type="text"
                        class="form-control"
                        id=""
                        placeholder=""
                        :class="classes"
                        v-model="checkoutData.user.name"
                      />
                      <span class="invalid-feedback">{{ errors[0] }}</span>
                    </validation-provider>
                  </div>
                  <div class="form-group col-md">
                    <validation-provider
                      :rules="{ regex: /^09[0-9]{8}$/, required: 'required' }"
                      v-slot="{ errors, classes }"
                    >
                      <label for=""
                        > <span class="text-danger">*</span></label
                      >
                      <input
                        type="tel"
                        class="form-control"
                        id=""
                        :class="classes"
                        placeholder=""
                        v-model="checkoutData.user.tel"
                      />
                      <span class="invalid-feedback">{{ errors[0] }}</span>
                    </validation-provider>
                  </div>
                </div>
                <div class="form-group">
                  <validation-provider
                    rules="required|email"
                    v-slot="{ errors, classes }"
                  >
                    <label for=""
                      > <span class="text-danger">*</span></label
                    >
                    <input
                      type="email"
                      class="form-control"
                      id=""
                      :class="classes"
                      placeholder=""
                      v-model="checkoutData.user.email"
                    />
                    <span class="invalid-feedback">{{ errors[0] }}</span>
                  </validation-provider>
                </div>
                <div class="form-group">
                  <validation-provider
                    :rules="{
                      regex: /^[\u4E00-\u9FA5-0-9]{5,}$/,
                      required: 'required',
                    }"
                    v-slot="{ errors, classes }"
                  >
                    <label for=""
                      > <span class="text-danger">*</span></label
                    >
                    <input
                      type="text"
                      class="form-control"
                      id=""
                      :class="classes"
                      placeholder=""
                      v-model="checkoutData.user.address"
                    />
                    <span class="invalid-feedback">{{ errors[0] }}</span>
                  </validation-provider>
                </div>
                <div class="form-group">
                  <label for="comments">()</label>
                  <textarea
                    class="form-control"
                    id="comments"
                    placeholder=": "
                    rows="2"
                    cols="5"
                    v-model="checkoutData.message"
                  ></textarea>
                </div>
                <div class="form-group">
                  <validation-provider
                    :rules="{
                      required: 'required',
                    }"
                  >
                    <label for=""
                      > <span class="text-danger">*</span></label
                    >
                    <select
