                      class="form-control"
                      v-model="checkoutData.payment_method"
                    >
                      <option value="" disabled selected></option>
                      <option value="transfer"></option>
                      <option value="credit_card"></option>
                    </select>
                  </validation-provider>
                </div>
                <div class="d-flex justify-content-between">
                  <a
