import Vue from 'vue';
import VueRouter from 'vue-router';
import axios from 'axios';
// $jquery
import $ from 'jquery';

import Layout from '@/views/front/Layout.vue';
import Home from '@/views/front/Home.vue';
import Products from '@/views/front/Products.vue';
import ProductDetail from '@/views/front/Product_detail.vue';
import Cart from '@/views/front/Cart.vue';
import Checkout from '@/views/front/Checkout.vue';

import Dashboard from '@/views/back/Dashboard.vue';
import ProductsList from '@/views/back/Products_list.vue';
import OrderList from '@/views/back/Order_list.vue';
import Coupons from '@/views/back/Coupons.vue';

Vue.use(VueRouter);

const routes = [
  {
    // 
    path: '*',
    redirect: '/home',
  },
  {
    path: '/home/:loaction?',
    //  children  name,DevTools
    // name: 'Layout',
    component: Layout,
    children: [
      {
        // '/'
        path: '',
        name: 'Home',
        component: Home,
      },
      {
        path: '/products/:category',
        name: 'Products',
        component: Products,
      },
      {
        path: '/product_detail/:id',
        name: 'Product_detail',
        component: ProductDetail,
      },
      {
        path: '/cart',
        name: 'Cart',
        component: Cart,
      },
      {
        path: '/checkout',
        name: 'Checkout',
        component: Checkout,
      },
    ],
  },
  {
    path: '/admin',
    //  children  name,DevTools
    // name: 'Dashboard',
    component: Dashboard,
    // 
    meta: { requiresAuth: true },
    //  '/admin/products_list'
    redirect: ProductsList,
    children: [
      {
        path: '/admin/products_list',
        name: 'Products_list',
        component: ProductsList,
        meta: { requiresAuth: true },
      },
      {
        path: '/admin/order_list',
        name: 'Order_list',
        component: OrderList,
        meta: { requiresAuth: true },
      },
      {
        path: '/admin/coupons',
        name: 'Coupons',
        component: Coupons,
        meta: { requiresAuth: true },
      },
    ],
  },

];

const router = new VueRouter({
  routes,
});

// 
router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    const api = `${process.env.VUE_APP_APIPATH}/api/user/check`;
    axios.post(api).then((response) => {
      // 
      if (response.data.success) {
        // modal
        $('#loginModal').modal('hide');

        next();
      } else {
        // modal
        $('#loginModal').modal('show');
        // 
        if (from.name !== 'Home') {
          // 
          router.push('/home');
        }
      }
    });
  } else {
    next();
  }
});

export default router;
