<template>
  <div>
    <LoadingPage :loadingStatus="status.pageIsLoading"></LoadingPage>

    <div class="container-fluid mt-5">
      <div class="row">
        <div class="col mt-5">
          <h1 class="h3 text-center mt-3 text-mainColor"></h1>
        </div>
      </div>

      <div class="row">
        <div class="col mt-3">
          <button class="btn btn--baseSet btn--secColor" @click="openModal(true)">
            
          </button>
        </div>
      </div>

      <!--  -->
      <div class="row">
        <div class="col">
          <table class="table table-hover mt-3">
            <thead class="thead-dark">
              <tr class="text-center">
                <th width="160"></th>
                <th width="160"></th>
                <th width="160"></th>
                <th width="160"></th>
                <th width="160"></th>
              </tr>
            </thead>
            <tbody>
              <tr
                class="text-center"
                v-for="(item, key) in coupons"
                :key="key"
                v-show="coupons.length"
              >
                <td class="align-middle">{{ item.title }}</td>
                <td class="align-middle">{{ item.percent }}%</td>
                <td class="align-middle">
                  {{ item.due_date | date }}
                </td>

                <td class="align-middle">
                  <span v-if="item.is_enabled" class="text-success"></span>
                  <span v-else class="text-muted"></span>
                </td>
                <td class="align-middle">
                  <a
                    href="#!"
                    title=""
                    @click.prevent="openModal(false, item)"
                  >
                    <i class="far fa-edit edit_icon"></i>
                  </a>
                  |
                  <a
                    href="#!"
                    title=""
                    class="align-middle"
                    @click.prevent="openDeleteModal(item)"
                  >
                    <i class="far fa-times-circle delete_icon pt-1"></i>
                  </a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- End  -->
    </div>

    <Pagination :pages="pagination" @emitPages="getCoupons"></Pagination>

    <!-- edit Modal -->
    <div
      class="modal fade"
      id="editModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="editModalLabel"
      aria-hidden="true"
      data-backdrop="static"
    >
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="editModalLabel">
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <form @submit.prevent="updateCoupon">
            <div class="modal-body">
              <div class="row">
                <div class="col-12">
                  <div class="form-row">
                    <div class="form-group col-md">
                      <label for="title"></label>
                      <input
                        type="text"
                        class="form-control"
                        id="title"
                        required
                        v-model="tempCoupon.title"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="form-group col-md">
                      <label for="code"></label>
                      <input
                        type="text"
                        class="form-control"
                        id="code"
                        required
                        v-model="tempCoupon.code"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="form-group col-md">
                      <label for="due_date"></label>
                      <input
                        type="date"
                        class="form-control"
                        id="due_date"
                        required
                        v-model="due_date[0]"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="form-group col-md">
                      <label for="percent"></label>
                      <input
                        type="number"
                        class="form-control"
                        id="percent"
                        placeholder=""
                        min="1"
                        max="100"
                        required
                        v-model="tempCoupon.percent"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="form-group col-md">
                      <div class="form-check">
                        <input
                          type="checkbox"
                          id="is_enabled"
                          class="form-check-input"
                          :true-value="1"
                          :false-value="0"
                          v-model="tempCoupon.is_enabled"
                        />
                        <label class="form-check-label" for="is_enabled"
                          ></label
                        >
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn--baseSet btn-outline--secColor"
                data-dismiss="modal"
              >
                
              </button>
              <button
                type="submit"
                class="btn btn--baseSet btn--mainColor updateBtn"
              >
                
                <i
                  class="fas fa-spinner fa-spin"
                  v-if="status.buttonIsLoading"
                ></i>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <!-- End edit Modal -->

    <!-- delete Modal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="cancelModalLabel"
      aria-hidden="true"
      data-backdrop="static"
    >
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="cancelModalLabel">
              <span>?</span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn--baseSet btn-outline--secColor"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn--baseSet btn--mainColor updateBtn"
              @click="deleteCoupon"
            >
              
              <i
                class="fas fa-spinner fa-spin"
                v-if="status.buttonIsLoading"
              ></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- End delete Modal -->
  </div>
</template>

<script>
// $jquery
import $ from 'jquery';

import LoadingPage from '@/components/common/Loading_page.vue';
import Pagination from '@/components/back/PaginationBack.vue';

export default {
  name: 'Coupons',
  components: {
    LoadingPage,
    Pagination,
  },
  data() {
    return {
      // 
      coupons: [],
      // 
      tempCoupon: {},
      // 
      isNew: false,
      // modal
      namedModal: '',
      // 
      due_date: [],
      // 
      status: {
        // 
        pageIsLoading: false,
        // 
        buttonIsLoading: false,
      },
      // 101
      pagination: {},
    };
  },
  methods: {
    getCoupons(page = 1) {
      const vm = this;
      // 
      vm.status.pageIsLoading = true;

      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`;
      vm.$http.get(api).then((response) => {
        // 
        vm.status.pageIsLoading = false;

        // 
        vm.coupons = response.data.coupons;
        // 
        vm.pagination = response.data.pagination;
      });
      // 
      $('html,body').scrollTop(0);
    },
    openModal(isNew, item) {
      const vm = this;
      //  updateCoupon()true;false
      vm.isNew = isNew;
      // 
      if (vm.isNew) {
        //  modal
        vm.tempCoupon = {};
        vm.due_date = [];
      } else {
        // 
        vm.tempCoupon = { ...item };
        // 
        const dateAndTime = new Date(vm.tempCoupon.due_date * 1000).toISOString().split('T');
        // 
        vm.due_date = [...dateAndTime];
      }

      //  modal
      $('#editModal').modal('show');
    },
    updateCoupon() {
      const vm = this;
      // 
      vm.status.buttonIsLoading = true;
      // 
      $('.updateBtn').attr('disabled', true);

      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`;
      //  post
      let httpMethod = 'post';
      // 
      if (!vm.isNew) {
        //  api
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        //  put
        httpMethod = 'put';
      }
      vm.$http[httpMethod](api, { data: vm.tempCoupon }).then((response) => {
        //  AlertMessage
        vm.$bus.$emit('messsage:push', response.data.message, 'success');

        //  modal
        $('#editModal').modal('hide');

        // 
        vm.getCoupons();

        // 
        vm.status.buttonIsLoading = false;
        // 
        $('.updateBtn').attr('disabled', false);
      });
    },
    //  modal
    openDeleteModal(item) {
      const vm = this;
      //  deleteCoupon()
      vm.tempCoupon = { ...item };

      // 
      $('#deleteModal').modal('show');
    },
    // 
    deleteCoupon() {
      const vm = this;

      // 
      vm.status.buttonIsLoading = true;
      // 
      $('#deleteModal').attr('disabled', true);

      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.$http.delete(api).then((response) => {
        // 
        if (response.data.success) {
          //  AlertMessage
          vm.$bus.$emit('messsage:push', response.data.message, 'success');
        } else {
          // 
          //  AlertMessage
          vm.$bus.$emit('messsage:push', response.data.message);
        }
        // 
        vm.getCoupons();

        // 
        vm.status.buttonIsLoading = false;
        // 
        $('#deleteModal').attr('disabled', false);

        //  modal
        $('#deleteModal').modal('hide');
      });
    },
  },
  watch: {
    // 
    due_date() {
      const vm = this;
      // ()
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      // 
      vm.tempCoupon.due_date = timestamp;
    },
  },
  mounted() {
    // 
    $('html,body').scrollTop(0);
  },
  created() {
    this.getCoupons();
  },
};
</script>
