<script>
// $jquery
import $ from 'jquery';

// @ is an alias to /src
import SwiperNavigation from '@/components/front/Swiper_navigation.vue';
import LoadingPage from '@/components/common/Loading_page.vue';

export default {
  name: 'Home',
  data() {
    return {
      // 
      contactData: {
        name: '',
        email: '',
        message: '',
        service: '',
      },
      // 
      status: {
        // 
        pageIsLoading: false,
      },
    };
  },
  components: {
    SwiperNavigation,
    LoadingPage,
  },
  methods: {
    // 
    submitContactData() {
      const vm = this;
      // vee-validateapi validate()
      vm.$refs.contactForm.validate().then((result) => {
        // 
        if (result) {
          // 
          vm.status.pageIsLoading = true;
          setTimeout(() => {
            // 
            vm.status.pageIsLoading = false;

            $('#contactModal').modal('show');
          }, 800);
        }
      });
    },
    //  modal
    closeModal() {
      $('#contactModal').modal('hide');

      // 
      $('#submitBtn').attr('disabled', true);
      this.$refs.contactForm.reset();
      setTimeout(() => {
        $('#contactData')[0].reset();
      }, 500);
    },
    // 
    goTo() {
      const vm = this;
      const goTo = vm.$route.params.loaction;
      // 
      if (goTo !== undefined) {
        // 
        let target = $(`#${goTo}`).offset().top;
        // px
        target -= 76;

        // 
        $('html,body').animate({ scrollTop: target }, 1500);
      } else {
        $('html,body').animate({ scrollTop: 0 }, 1500);
      }
    },
  },
  watch: {
    // 
    $route() {
      this.goTo();
    },
  },
  mounted() {
    // 
    $('html,body').scrollTop(0);
    this.goTo();

    $('body').addClass('front');
  },
};
</script>

