<template>
  <div>
    <LoadingPage :loadingStatus="status.pageIsLoading"></LoadingPage>

    <div class="container-fluid mt-5">
      <div class="row">
        <div class="col mt-5">
          <h1 class="h3 text-center mt-3 text-mainColor"></h1>
        </div>
      </div>

      <div class="row">
        <div class="col mt-3">
          <button
            class="btn btn--baseSet btn--secColor"
            @click="openModal(true)"
          >
            
          </button>
        </div>
      </div>

      <!--  -->
      <div class="row">
        <div class="col">
          <table class="table table-hover mt-3">
            <thead class="thead-dark">
              <tr class="text-center">
                <th width="160"></th>
                <th></th>
                <th width="160" class="text-right"></th>
                <th width="160"></th>
                <th width="160"></th>
              </tr>
            </thead>
            <tbody>
              <tr class="text-center" v-for="item in products" :key="item.id">
                <td class="align-middle">{{ item.category }}</td>
                <td class="align-middle text-left pl-5">
                  <img :src="item.imageUrl" alt="" class="img" /><span
                    class="pl-2 text-secColor"
                    >{{ item.title }}</span
                  >
                </td>
                <td class="align-middle text-right">
                  {{ item.price | currency }}
                </td>
                <td class="align-middle">
                  <span v-if="item.is_enabled" class="text-success"
                    ></span
                  >
                  <span v-else></span>
                </td>
                <td class="align-middle">
                  <a
                    href="#!"
                    title=""
                    @click.prevent="openModal(false, item)"
                  >
                    <i class="far fa-edit edit_icon"></i>
                  </a>
                  |
                  <a
                    href="#!"
                    title=""
                    class="align-middle"
                    @click.prevent="openDeleteModal(item)"
                  >
                    <i class="far fa-times-circle delete_icon pt-1"></i>
                  </a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- End  -->
    </div>

    <Pagination :pages="pagination" @emitPages="getProducts"></Pagination>

    <!-- edit Modal -->
    <div
      class="modal fade"
      id="editModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="editModalLabel"
      aria-hidden="true"
      data-backdrop="static"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="editModalLabel">
              <span v-if="isNew"></span>
              <span v-else></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <form @submit.prevent="updateProduct">
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="image"
                      > <span class="text-danger">*</span></label
                    >
                    <input
                      type="text"
                      class="form-control"
                      id="image"
                      required
                      v-model="tempProduct.imageUrl"
                    />
                  </div>
                  <div class="form-group">
                    <label for="customFile"
                      >  <span class="text-danger">*</span>
                      <i
                        class="fas fa-spinner fa-spin"
                        v-if="status.fileUploading"
                      ></i>
                    </label>
                    <input
                      type="file"
                      id="customFile"
                      class="form-control customFile"
                      ref="files"
                      @change="uploadFile"
                    />
                  </div>
                  <img class="img-fluid" :src="tempProduct.imageUrl" alt="" />
                </div>
                <div class="col-sm-8">
                  <div class="form-group">
                    <label for="title"
                      > <span class="text-danger">*</span></label
                    >
                    <input
                      type="text"
                      class="form-control"
                      id="title"
                      required
                      v-model="tempProduct.title"
                    />
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category"
                        > <span class="text-danger">*</span></label
                      >
                      <select
                        name=""
                        id="category"
                        class="form-control"
                        required
                        v-model="tempProduct.category"
                      >
                        <option value="" disabled selected></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"
                        > <span class="text-danger">*</span></label
                      >
                      <input
                        type="number"
                        class="form-control"
                        id="price"
                        min="100"
                        required
                        v-model="tempProduct.price"
                      />
                    </div>
                  </div>

                  <div class="form-group">
                    <label for="description"
                      > <span class="text-danger">*</span></label
                    >
                    <textarea
                      type="text"
                      class="form-control"
                      id="description"
                      rows="5"
                      placeholder=""
                      required
                      v-model="tempProduct.description"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <label for="content"
                      > <span class="text-danger">*</span></label
                    >
                    <textarea
                      type="text"
                      class="form-control"
                      id="content"
                      rows="3"
                      placeholder=""
                      required
                      v-model="tempProduct.content"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        v-model="tempProduct.is_enabled"
                        :true-value="1"
                        :false-value="0"
                        id="is_enabled"
                      />
                      <label class="form-check-label" for="is_enabled">
                        
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn--baseSet btn-outline--secColor"
                data-dismiss="modal"
              >
                
              </button>
              <button
                type="submit"
                class="btn btn--baseSet btn--mainColor updateBtn"
              >
                
                <i
                  class="fas fa-spinner fa-spin"
                  v-if="status.buttonIsLoading"
                ></i>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <!-- End edit Modal -->

    <!-- delete Modal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="deleteModalLabel"
      aria-hidden="true"
      data-backdrop="static"
    >
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="deleteModalLabel">
              <span>?</span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn--baseSet btn-outline--secColor"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn--baseSet btn--mainColor updateBtn"
              @click="deleteProduct()"
            >
              
              <i
                class="fas fa-spinner fa-spin"
                v-if="status.buttonIsLoading"
              ></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- End delete Modal -->
  </div>
</template>

<script>
// $jquery
import $ from 'jquery';

import LoadingPage from '@/components/common/Loading_page.vue';
import Pagination from '@/components/back/PaginationBack.vue';

export default {
  name: 'Products_list',
  components: {
    LoadingPage,
    Pagination,
  },
  data() {
    return {
      // 
      products: [],
      // 
      tempProduct: {},
      // 
      isNew: false,
      // 
      tempDeleteItem: {},
      // 
      status: {
        // 
        pageIsLoading: false,
        // 
        fileUploading: false,
        // 
        buttonIsLoading: false,
      },
      // 101
      pagination: {},
    };
  },
  methods: {
    // 
    getProducts(page = 1) {
      const vm = this;
      // 
      vm.status.pageIsLoading = true;

      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`;
      vm.$http.get(api).then((response) => {
        // 
        if (response.data.success) {
          // 
          vm.status.pageIsLoading = false;

          // 
          vm.products = response.data.products;
          // 
          vm.pagination = response.data.pagination;
        } else {
          // 
          vm.signout();
        }
      });
    },
    // 
    signout() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/logout`;
      vm.$http.post(api).then((response) => {
        // 
        if (response.data.success) {
          vm.$router.push({ name: 'Home' });
        }
      });
    },
    //  modal
    //  isNew:; item: 
    openModal(isNew, item) {
      const vm = this;
      //   updateProduct()
      vm.isNew = isNew;
      //  modal
      vm.tempProduct = {};
      //  modal
      document.getElementById('customFile').value = '';
      //  product
      vm.tempProduct = { ...item };

      //  modal
      $('#editModal').modal('show');
    },
    // 
    updateProduct() {
      const vm = this;

      // 
      vm.status.buttonIsLoading = true;
      // 
      $('.updateBtn').attr('disabled', true);

      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
      let httpMethod = 'post';
      // 
      if (!vm.isNew) {
        //  api
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        //  put
        httpMethod = 'put';
      }
      vm.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {
        // 
        if (response.data.success) {
          //  AlertMessage
          vm.$bus.$emit('messsage:push', response.data.message, 'success');

          //  modal
          $('#editModal').modal('hide');

          // 
          vm.getProducts();
        } else {
          // 
          //  AlertMessage
          vm.$bus.$emit('messsage:push', response.data.message);

          //  modal
          $('#editModal').modal('hide');

          // 
          vm.getProducts();
        }
        // 
        vm.status.buttonIsLoading = false;
        // 
        $('.updateBtn').attr('disabled', false);
      });
    },
    //  modal
    openDeleteModal(item) {
      const vm = this;
      // 
      $('#deleteModal').modal('show');

      //  deleteProduct()
      vm.tempDeleteItem = item;
    },
    // 
    deleteProduct() {
      const vm = this;
      // 
      vm.status.buttonIsLoading = true;

      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempDeleteItem.id}`;
      vm.$http.delete(api).then((response) => {
        // 
        if (response.data.success) {
          //  AlertMessage
          vm.$bus.$emit('messsage:push', response.data.message, 'success');
        } else {
          // 
          //  AlertMessage
          vm.$bus.$emit('messsage:push', response.data.message);
        }
        // 
        vm.getProducts();

        // 
        vm.status.buttonIsLoading = false;

        //  modal
        $('#deleteModal').modal('hide');
      });
    },
    // 
    uploadFile() {
      const vm = this;
      // 
      vm.status.fileUploading = true;

      // 
      const uploadedFile = vm.$refs.files.files[0];
      //  web api FormData()form
      // https://developer.mozilla.org/en-US/docs/Web/API/FormData/FormData
      const formData = new FormData();
      //  append
      formData.append('file-to-upload', uploadedFile);
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;
      vm.$http
        .post(api, formData, {
          //  enctype="multipart/form-data" 
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
        .then((response) => {
          // 
          if (response.data.success) {
            //  tempProduct.imageUrl $set
            // (,,)
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
          } else {
            //  AlertMessage
            vm.$bus.$emit('messsage:push', response.data.message, 'danger');
          }

          // 
          vm.status.fileUploading = false;
        });
    },
  },
  mounted() {
    // 
    $('html,body').scrollTop(0);
  },
  created() {
    this.getProducts();
  },
};
</script>

<style lang="scss" scoped>
// table 
.table {
  // 
  .img {
    width: 100px;
    height: 80px;
  }
}

// modal 
.modal {
  .customFile {
    height: auto;
  }
}
</style>
